$primary-color: #33333570;
$text-color: bisque;

.footer {
  position: static;
  bottom: 0;
  width: 375px;
  display: flex;
  justify-content: space-between;
  background-color: $primary-color;
  border-radius: 8px;
  margin-top: 30px;
  // padding: 0 20px;
}
.footerContent {
  display: flex;
  color: bisque;
  flex-direction: column;
  justify-content: space-between;
  align-items: flex-start;
  height: 200px;
  padding: 20px;
}
.logoBox {
  display: flex;
  align-items: center;
  justify-content: center;
}
.logo {
  height: 50px;
  width: 70px;
}
.logoText {
  color: $text-color;
  font-size: 24px;
}
.byApi {
  display: flex;
  justify-content: center;
  align-items: center;
}
.tmdb {
  display: flex;
  justify-content: center;
  align-items: center;
  font-size: 24px;
  font-weight: bold;
  color: bisque;
  margin-left: 10px;
  text-shadow: 0px 0px 5px rgba(20, 220, 220, 0.914);
}
.tmdbAccent {
  margin-left: 10px;
  width: 60px;
  height: 25px;
  border-radius: 50px;
  background-color: bisque;
  box-shadow: inset 0px 0px 5px rgba(20, 220, 220, 0.914);
}

