@media screen and (min-width: 768px) {
  .footer {
    width: 768px;
    bottom: auto;
    padding: 0 20px;
  }
}
//=======================================
@media screen and (min-width: 1440px) {
  .footer {
    width: 1020px;
    padding: 0 20px;
  }
}
